import InputCell from '@/components/InputCell/InputCell.vue'
import UploadItem from '@/components/UploadItem/UploadItem.vue'
export default {
	data() {
		return {
			isEdit: true, // 是否编辑状态
			resume: {
				name: '', // 公司名称
				cityCode: '', // 城市码
				address: '', // 地址
				industryCode: '', // 所在行业
				contactName: '', // 招聘联系人
				businessImg: '', // 营业执照
				companyImg: '', // 工作场景
				logo: '', // 公司logo
				phone: '', // 电话号码
			},
			pictureIndex: 0,
			verifyText: '发送验证码',
			cityCodeText: '', // 区域文字
			citySelected: [], // 已选中的城市
		}
	},
	components: {
		InputCell,
		UploadItem
	},
	onLoad(options){
		if(options.isEdit === 'false'){
			this.isEdit = false
		}
		if(options.isEdit === 'true'){
			this.isEdit = true
		}
	},
	methods:{
		chooseImage(){
			// 让用户选择相册或者拍照
			uni.chooseImage({
				count:1,
				sizeType: 'compressed',
				sourceType: ['album', 'camera'],
				success: (res) => {
					this.avatar = res.tempFilePaths[0]
				}
			})
		},
		cityPickerChange(e){
			const { code, value } = e.detail
			this.resume.cityCode = code
			this.cityCodeText = Array.from( new Set(value)).join('')
		},
		uploadImage(){
			uni.chooseImage({
				count:1,
				sizeType: 'compressed',
				sourceType: ['album', 'camera'],
				success: (res) => {
					this.avatar = res.tempFilePaths[0]
				}
			})
		}
	}
}
